
























































































import service from '../../../service.js';
export default {
	data() {
		return {
			id:0,
			peo_id:'-',
			datetime:'-',
			order_id:0,
			money:0,
			area:0,
			imgList:[],
			aname:'-',
			project_name:'-',
			address:'-',
			// fmoney:0,
			// maketype:0,
		};
	},
	onLoad(option) {
		if(option.id){
			this.id = option.id
		}else{
			uni.navigateBack({
				delta:1
			})
		}
	},
	mounted(){
		this.initData()
	},
	methods:{
		initData(){
			let self = this;
			uni.showLoading()
			
			uni.request({
				url:service.BASEURL+'/home/share/case_anfos',
				method:"POST",
				data:{
					id:this.id
				},
				success(res) {
					console.log(res);
					if(!res.data || res.data.code != 1){
							uni.showToast({
							title:res.data.msg,
							icon:'none'
						})
					}else{
						if(!res.data || res.data.code !=1){
							uni.showToast({
								title: res.data.msg,
								icon:'none'
							});
						}else{
							let info = res.data.data;
							self.peo_id = info.peo_id;
							self.datetime = info.datetime;
							self.order_id = info.order_id;
							self.money = info.money;
							// self.fmoney = info.fmoney;
							self.area = info.area;						
							if(info.img){
								self.imgList = info.img.map(e=>(service.BASEURL+e.url));
							}
							self.aname = info.aname;
							self.project_name = info.project_name;
							self.address = info.address;						
						}
					}
				},
				complete() {
					setTimeout(function() {uni.hideLoading()}, 1500);
				}
			})
		},
	}
}
